.accordion {
  width: 100%;
  max-width: 480px;
  margin: 0 auto;
  position: relative;
}
.panel {
  background-color: #57DFB7;
  border-radius: 12px;
margin-top: 10px;
}
.panel__label {
  display: block;
  width: 100%;
  background: none;
  border-radius: 12px;
  border: none;
  text-align: left;
  padding: 27px 65px 27px 30px;
  font-weight: bold;
  font-size: 18px;
  font-family: inherit;
  transition: color 0.1s linear;
  cursor: pointer;
  color: #012A43;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

}
.panel__label:focus {
  outline: none;
}
.panel__label:after,
.panel__label:before {
  content: "";
  position: absolute;
  right: 25px;
  top: 50%;
  width: 22px;
  height: 2px;
  margin-top: -2px;
  background-color: #372717;
}
.panel__label:before {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  transition: -webkit-transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1);
  transition: transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1);
  transition: transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1), -webkit-transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1);
}
.panel[aria-expanded="true"] .panel__content {
  opacity: 1;
}
.panel[aria-expanded="true"] .panel__label:before {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
.panel__inner {
  overflow: hidden;
  will-change: height;
  transition: height 0.4s cubic-bezier(0.65, 0.05, 0.36, 1);
}
.panel__content {
  margin: 5px 25px 25px;
  font-size: 17px;
  color: #4C6371;
  opacity: 0;
  transition: opacity 0.3s linear 0.18s;
}
.panel:not(:last-child) {
  margin-bottom: 3px;
}

